<template>
	<div v-theme:column="'narrow'" class="show-blog">
		<h1>博客总览</h1>
		<input type="text" v-model="search" placeholder="搜索" />
		<div class="single-blog" v-for="blog in filterdBlogs">
			<router-link v-bind:to="'/blog/' + blog.id"><h2 v-rainbow>{{blog.title | to-uppercase}}</h2></router-link>
			<article>{{blog.content | snippet}}</article>
		</div>
	</div>
</template>

<script>
export default {
	name:'show-blog',
	data () {
		return{
			blogs:[],
			search:""
		}
	},
	created(){
		this.$http.get("https://wd2171315040zepbeu.wilddogio.com/posts.json")
			.then(function(data){
				return data.json();
//     			console.log(data.json());	
//	            this.blogs = data.body.slice(0,10);
//	            console.log(this.blogs);
		    })
		    .then(function(data){
		    	var blogsArray = [];
		    	for(let key in data){
//		    		console.log(data[key]);
                    data[key].id = key;
                    blogsArray.push(data[key]);
		    	}
//		    	console.log(blogsArray)
		    	this.blogs = blogsArray;
		    	console.log(this.blogs);
		    })
	},
	computed:{
		filterdBlogs:function(){
			return this.blogs.filter((blog) => {
				return blog.title.match(this.search);
			})
		}
	},
	filters:{
		"to-uppercase":function(value){
			return value.toUpperCase()
		}
	},
	directives:{
		'rainbow':{
			bind(el,bingding,vnode){
				el.style.color = "#" + Math.random().toString(16).slice(2,8);
			}
		}
	}

}
</script>

<style scoped>
	.show-blog{ max-width: 800px; margin: 0 auto;}
	.single-blog{ padding: 20px; margin: 20px 0; box-sizing: border-box; background-color: #eee; border: 1px dotted #aaa;}
    input{ width: 100%; padding: 6px; box-sizing: border-box;}
    .show-blog a{ text-decoration: none; color: #444;}
</style>


