<template>
    <div class="single-blog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者：{{blog.author}}</p>
        <ul>
        	<li v-for="category in blog.categories">
        		{{category}}
        	</li>
        </ul>
    </div>
</template>

<script>
	export default{
		name:'single-blog',
		data(){
			return{
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			this.$http.get('https://wd2171315040zepbeu.wilddogio.com/posts/' + this.id + ".json")
			.then(function(data){
				console.log(data);
				this.blog = data.body;
			})
		}
	}
</script>

<style scoped>
	.single-blog{max-width: 960px; margin: 0 auto; padding: 20px; background-color: #eee; border: 1px dotted #aaa;}
</style>


