<template>
	<div class="add-blog">
		<h1>添加博客</h1>
		<form v-if="!submmited">
			<label>博客标题</label>
			<input type="text" v-model="blog.title" required />
		
		    <label>博客内容</label>
		    <textarea v-model="blog.content"></textarea>
		    
		    <div id="checkboxs">
		    	<input type="checkbox" value="Vue.js" v-model="blog.categories" />
		    	<label>Vue.js</label>
		    	<input type="checkbox" value="Node.js" v-model="blog.categories" />
		    	<label>Node.js</label>
		    	<input type="checkbox" value="React.js" v-model="blog.categories" />
		    	<label>React.js</label>
		    	<input type="checkbox" value="Angular4.js" v-model="blog.categories" />
		    	<label>Angular4.js</label>
		    </div>
		    
		    <label>作者：</label>
		    <select v-model="blog.author">
		    	<option v-for="author in authors">{{author}}</option>
		    </select>
		    <button v-on:click.prevent="post">添加博客</button>
		</form>
		
		<div class="">
			<h3 v-if='submmited'>您的博客发布成功！</h3>
		</div>
			
		<div id="preview">
			<h3>博客总览</h3>
			<p>博客标题：{{blog.title}}</p>
			<p>博客内容：{{blog.content}}</p>
			<p>博客分类：</p>
			<ul>
				<li v-for="categorie in blog.categories">
					{{categorie}}
				</li>
			</ul>
			<p>作者：{{blog.author}}</p>
		</div>
	</div>
</template>

<script>
export default {
	name:'add-blog',
	data () {
		return{
			blog:{
				title:"",
				content:"",
				categories:[],
				author:""
			},
			authors:["Hemiah","Henry","Bucky"],
			submmited:false
		}
	},
	methods:{
		post:function(){
			this.$http.post("https://wd2171315040zepbeu.wilddogio.com/posts.json",this.blog)
				.then(function(data){
					console.log(data)
					this.submmited = true;
				})
		}
	}
}
</script>

<style scoped>
	.add-blog *{ box-sizing: border-box;}
	.add-blog{ max-width: 600px; margin: 20px auto; padding: 20px;}
	label{ display: block; margin: 20px 0 10px;}
	input[type="text"],textarea,select{ width: 100%; display: block; padding: 8px;}
    textarea{ height: 200px;}
    #checkboxs label{ display: inline-block; margin-top: 0; margin-right: 10px;}
    #checkboxs input{ display: inline-block;}
    button{ display: block; margin: 20px 0; background: crimson; color: #fff; border: 0; padding: 14px; border-radius: 4px; font-size: 18px; cursor: pointer;}
    #preview{ border: 1px dotted #ccc; padding: 10px 20px; margin: 30px 0;}
    h3{ margin-bottom: 10px;}
</style>


